import React, {Component} from "react";
import {connect} from "react-redux";
import {
    consentsDataInRequest,
    getAgreementType,
    getConsentsProps,
    getCurrentStepId,
    getCustomerNoEmail,
    getFrontValidationMsg,
    getModifyConsentsInCartQueue,
    getRegisteredCheckoutConditions,
    getSalesOfGoodsProcess,
    getSelectedDeliveryMethod,
    isPrintConsentDocumentsButtonEnabled
} from "../../selectors";
import {
    changeConsentState,
    pushToModifyConsentsInCartQueue,
    setPermanentlyAgreedConsentsVisibleForGroup
} from "../../actions/app";
import {
    clearModifyConsentsInCartQueue,
    printConsents,
    printMobileConsents,
    registerCmsConsentConfig,
    registerNextStepCondition,
    registerPrintConsentValidator,
    registerVerificationConsentType,
    requestCartConsentsData,
    unregisterNextStepCondition,
    updateConsentGroupExpanded,
    updateConsentsStates,
    updateValueConsentGroupExpanded
} from "../../actions/data";
import {getLegalForm} from "eshop/modules/checkout/selectors"
import {getCartIsNet} from "eshop/modules/cart/selectors"
import {Expander} from "eshop/modules/core/components/ui/Expander";
import MulticartConsentsGroupSection from "./MulticartConsentsGroupSection";
import MulticartSingleConsentRow from "./MulticartSingleConsentRow";
import MulticartValidationComponent from "../MulticartValidationComponent";
import {GenericPopup, OraButton, OraLoader, OraSimpleCheckbox} from "eshop/components/OraCommonComponents";
import _ from "lodash";
import {consentIsPermanentlyAgreed, validateConsent} from "../../utils/utils";
import ExpanderFromHtml from "eshop/modules/core/components/ui/ExpanderFromHtml";
import LegalForm from "../../constants/LegalFormEnum";
import OraModal from "eshop/modules/core/components/ui/Modal";
import OnlineUtils from "eshop/utils/OnlineUtils";
import {getOrCreateDocument} from "../../../documents/actions/documents";
import {getDocumentItems} from "../../../documents/selectors";
import {isLogged} from "eshop/modules/auth/selectors/authorization";

export class MulticartConsentsView extends Component {
    constructor(props) {
        super(props);
        this.elementId = this.props.cmpUid + "_loaderParent";
        this.onUpdateForSingleConsent = this.onUpdateForSingleConsent.bind(this);
        this.debouncedStateUpdate = _.debounce(()=>{
                this.props.clearModifyConsentsInCartQueue()
            }, 1);
        this.debouncedStateUpdate = this.debouncedStateUpdate.bind(this);
   }

    componentWillMount() {
        this.registerVerificationConsentType(null);
    }
    componentDidMount() {
        OPL.UI.loadModules(document.getElementById(this.elementId), {
            path: 'core/modules/loader',
            options: '{"fadeDuration":120}'
        });

        this.props.registerNextStepCondition('consents');
        this.props.registerCmsConsentConfig(this.props.consentTypesList);
        console.warn("isNet on component mount:");
        console.warn(this.props.isNet );

        console.warn("legalForm on component mount:");
        console.warn(this.props.legalForm );

        this.props.requestCartConsentsData();
    }

    componentDidUpdate(prevProps)
    {
        const legalFormInRequest = this.props.consentsDataInRequest.legalForm || "";
        if (this.props.isNet && this.props.legalForm && this.props.legalForm !== legalFormInRequest) {
            console.warn("Request consents on component update: market B2B, legal form " + this.props.legalForm);
            this.props.requestCartConsentsData();
        }

        if (prevProps.isLogged != null && prevProps.isLogged !== this.props.isLogged && this.props.isLogged) {
            console.warn("Request consents on customer logged in", this.props.isLogged);
            this.props.requestCartConsentsData();
        }

        if (this.printDocumentButtonIsVisible.apply(this)) {
            this.registerConsentDocumentsCheckoutCondition.apply(this);
        } else {
            this.unregisterConsentDocumentsCheckoutCondition.apply(this);
        }

        this.registerVerificationConsentType(prevProps);
    }

    printDocumentButtonIsVisible() {
        if (this.props.cartContainsFixNonActivationProcess) {
            return false
        }
        if (!this.props.salesChannelsToShowPrintButton.includes(this.props.channels.sales)) {
            return false;
        }
        if (this.props.consentProps.consents.length !== 0) {
            return this.props.consentProps.consents.filter(consent => consent.type === this.props.verificationConsentType).length > 0;
        }
        return false;
    }

    registerConsentDocumentsCheckoutCondition() {
        if (!this.props.registeredCheckoutConditions['consentDocuments']) {
            this.props.registerNextStepCondition('consentDocuments');
            this.props.registerPrintConsentValidator(this.props.printConsentValidatorByConsentType);
        }
    }

    unregisterConsentDocumentsCheckoutCondition() {
        if (this.props.registeredCheckoutConditions['consentDocuments']) {
            this.props.unregisterNextStepCondition('consentDocuments');
        }
    }

    registerVerificationConsentType(prevProps){
        const verificationConsentType = prevProps ? prevProps.verificationConsentType : null;
        if(verificationConsentType !==this.props.verificationConsentType){
            this.props.registerVerificationConsentType(this.props.verificationConsentType);
        }
    }

    componentWillUnmount() {
        OPL.UI.stopModules(document.getElementById(this.elementId));
    }

    setCheckboxConsentStateWithoutRemoteCall(data){
        console.log("Locally set consents",data);
        this.changeConsentState(data)

    }

    onUpdateForSingleConsent(data){
        this.props.pushToModifyConsentsInCartQueue(data);
        this.setCheckboxConsentStateWithoutRemoteCall(data);
        this.debouncedStateUpdate()
    }

     onConsentGroupExpanded(listNo){
        this.props.updateConsentGroupExpanded(listNo);
     }

    getPropsForConsentGroup(consents) {
        return {
            consents: consents,
            consentStates:this.props.consentProps.consentStates,
            consentsErrorList:this.props.errorList && this.props.errorList.find(e => "consents" === e.type),
            disableInnerMargin: true,
            readOnly: this.props.descriptions.readOnly,
            isUpdating: this.props.consentProps.updatingAnyConsent,
            updatingConsents: this.props.consentProps.updatingConsents,
            onChange: this.changeConsentState.bind(this),
            onUpdate: this.onUpdateForSingleConsent,
            agreeAll: this.props.descriptions.selectAllLabel,
            loaderElementId: this.elementId,
            factoryType: this.props.factoryType,
            selectedMethod: this.props.selectedMethod,
            errorList:this.props.errorList,
            onExpanded: this.onConsentGroupExpanded.bind(this),
            onExpandedInit: this.props.updateValueConsentGroupExpanded.bind(this)
        };
    }

    getPropsForConsent(consentData) {
        return {
            key: consentData.consentCode,
            readOnly: this.props.descriptions.readOnly,
            consentsWithBonusLoading: consentData.isRelatedWithBonus,
            isUpdating: this.props.consentProps.updatingAnyConsent,
            isMandatory: consentData.required,
            consent: consentData,
            state: this.props.consentProps.consentStates.filter((consentState) => (consentState.consentCode === consentData.consentCode)),
            updating: this.props.consentProps.updatingConsents[consentData.consentCode],
            onChange: this.changeConsentState.bind(this),
            onUpdate: this.props.updateConsentsStates,
            formInputType: this.props.formInputType,
            loaderElementId: this.elementId
        };
    }

    getPropsForCheckbox(){
        return{
            labelClassName:"o-checkbox opl-checkbox opl-checkbox--black",
            name: "agree_all",
            text: this.props.descriptions.selectAllLabel,
            checked: this.isAllSelected(),
            onChange: this.agreeAllCheckboxChange.bind(this),
            readOnly: this.props.consentProps.updatingAnyConsent || this.props.clearingModifyConsentsInCartQueueInProgress
        };
    }

    isAnyReadOnly(){
        if(this.getMatchingConsents().length === 0) {
            return false;
        }

        let anyReadOnly = false;
        this.getMatchingConsents().forEach(consent => {
            if(consent.readonly)
                anyReadOnly=true;
        });
        return anyReadOnly;
    }

    changeConsentState(data) {
        if(data.length > 0) {
            let changableConsents=data.filter(consent => !this.checkReadOnly(consent));
            if(this.isConfirmationNeeded(data)){
                if(changableConsents.filter(consent=>!this.isConfirmationNeeded(consent)).length) {
                    this.props.changeConsentState(changableConsents.filter(consent => !this.isConfirmationNeeded(consent)));
                }
                this.setState({showConfirmationModal:true,confirmableData:changableConsents.filter(consent=>this.isConfirmationNeeded(consent))});
            }else{
                this.props.changeConsentState(changableConsents);
            }
        }
    }
    confirmedChangeConsentState() {
        let data = this.state.confirmableData;
        if(data.length > 0) {
                this.props.changeConsentState(data.filter(consent => !this.checkReadOnly(consent)));
        }
    }
    confirmationPopupProps(){
        if(this.state) {
            return {
                title:this.props.descriptions&&this.props.descriptions.warningModalTitle,
                content: this.getConfirmationMessage(),
                onClickConfirm: this.confirmedChangeConsentState.bind(this)
            }
        }
    }

    getConfirmableConsentTypeList(){
        let consentTypesLists=[];
        this.state.confirmableData.forEach(consent=>{
            let consentTypesList=this.props.consentTypesList.find(list=>list.confirmationRequiredConsentCodes.includes(consent.consentCode));
            if(consentTypesList){
                OnlineUtils.addUniqueIntoArray(consentTypesList,consentTypesLists);
            }
        });
        return consentTypesLists;
    }
    getConfirmationMessage(){
        if(this.state && this.state.confirmableData){
            let consentTypesLists=this.getConfirmableConsentTypeList();
            if(consentTypesLists.length && consentTypesLists.find(list=>!!list.confirmationMessage)){
                return consentTypesLists.find(list=>!!list.confirmationMessage).confirmationMessage;
            }
        }
        return "";
    }
    renderConfirmationModal(){
            return (
                <OraModal
                    id={"consent-confirmation-modal"}
                    size="narrow"
                    escapeClose={true}
                    showClose={true}
                    clickClose={true}
                    open={this.state && this.state.showConfirmationModal}
                    onClose={this.onCloseConfirmationModal.bind(this)}>
                    <GenericPopup id={"consent-confirmation-in-modal"} {...this.confirmationPopupProps()} />
                </OraModal>)
    }

    isConfirmationNeeded(data) {
        if (data) {
            if (Array.isArray(data)) {
                return (!!data.filter(consent => this.isConfirmationNeeded(consent)).length)
            } else {
                if (data.stateCode.indexOf("NO_") > -1 || data.stateCode.indexOf("_NO")>-1) {
                    return false;
                } else {
                    let consentsList = this.props.consentTypesList.find(list => list.consentsCode.includes(data.consentCode));
                    if (consentsList && consentsList.confirmationRequiredConsentCodes) {
                        if (consentsList.confirmationRequiredConsentCodes.includes(data.consentCode)) {
                            return true;
                        }
                    }
                }
            }
        }
        return false;
    }

    onCloseConfirmationModal(){
        this.setState({showConfirmationModal:false});
    }


    checkReadOnly(consentToCheck){
        var result = false;
        this.getVisibleConsents().filter(consent => {
            if(consent.consentCode === consentToCheck.consentCode){
                result = !!consent.readonly;
            }
        });

        return result;
    }

    getMatchingConsents() {
        return this.getVisibleConsents().filter(consent => {
            let matchAnyCondition = false;
            this.props.consentTypesList.forEach(condition => {
                matchAnyCondition = matchAnyCondition || this.matchCondition(consent, condition);
            });
            return matchAnyCondition;
        });
    }

    isAllSelected() {
        if(this.getMatchingConsents().length === 0) {
            return false;
        }

        let allSelected = true;
        let state;
        let positiveState;
        let selectedPositive;
        this.getMatchingConsents().forEach(consent => {
            if(!consent.readonly){
                positiveState = consent.states.find((state) => state.positive);
                if(!!consent.bundleAssignments && consent.bundleAssignments.length > 0) {
                    consent.bundleAssignments.forEach(ba => {
                        state = this.props.consentProps.consentStates.find((consentState) => (consent.consentCode === consentState.consentCode && consentState.bundleNo === ba.bundleNo));
                        selectedPositive = ((state !== undefined) && (state.stateCode === positiveState.code));
                        allSelected = allSelected && selectedPositive;
                    });
                } else if(!!consent.msisdns && consent.msisdns.length > 0) {
                    consent.msisdns.forEach(ba => {
                        state = this.props.consentProps.consentStates.find((consentState) => (consent.consentCode === consentState.consentCode && consentState.bundleNo === ba.bundleNo));
                        selectedPositive = ((state !== undefined) && (state.stateCode === positiveState.code));
                        allSelected = allSelected && selectedPositive;
                    });
                }else{
                        state = this.props.consentProps.consentStates.find((consentState) => (consent.consentCode === consentState.consentCode && !consentState.bundleNo));
                        selectedPositive = ((state !== undefined) && (state.stateCode === positiveState.code));
                        allSelected = allSelected && selectedPositive;
                }
            }
        });

        return allSelected;
    }

    getConsentMapping(relatedWithBonus, checkConsents) {
        let nullStateNotAllowedConsentType = ["OSW"];
        let matchingConsents = [];
        this.getMatchingConsents().filter(consent => consent.isRelatedWithBonus == relatedWithBonus || this.isRelatedWithBonusBySubsection(consent.consentCode))
            .forEach(consent => {
                let nullStateNotAllowed = (nullStateNotAllowedConsentType.indexOf(consent.type) !== -1);
                let stateCode = null;
                if(nullStateNotAllowed){
                    stateCode = consent.states.find((state) => state.positive == checkConsents).code;
                } else {
                    stateCode = checkConsents ? consent.states.find((state) => state.positive).code : consent.states.find((state) => !state.positive).code;
                }
                if(!consent.bundleAssignments || consent.bundleAssignments.length === 0) {
                    if(!!consent.msisdns && consent.msisdns.length > 0){
                        consent.msisdns.forEach(bundleConsent => {
                            matchingConsents.push({consentCode: consent.consentCode, bundleNo: bundleConsent.bundleNo, stateCode: stateCode});
                        })
                    }else {
                        matchingConsents.push({consentCode: consent.consentCode, bundleNo: null, stateCode: stateCode});
                    }
                } else {
                    matchingConsents = matchingConsents.concat(
                        consent.bundleAssignments.map(ba => ({consentCode: consent.consentCode, bundleNo: ba.bundleNo, stateCode: stateCode}))
                    );
                }
            });
        return matchingConsents;
    }

    isRelatedWithBonusBySubsection(consentCode){
        return this.props.consentProps.conf.some(conf => {
             return (conf.sections || [])
             .filter(s => s.withBonus)
             .map(s=>s.consentsCode)
             .some(
                cCodes=>cCodes.indexOf(consentCode) > -1
             )
        })
    }

    acceptAllConsents() {
        let bonusConsents = this.getConsentMapping(true, true);
        let otherConsents = this.getConsentMapping(false, true);
        this.changeConsentState(otherConsents.concat(bonusConsents));
        this.props.updateConsentsStates(bonusConsents);
    }

    uncheckAllConsents() {
        let bonusConsents = this.getConsentMapping(true, false);
        let otherConsents = this.getConsentMapping(false, false);
        this.changeConsentState(otherConsents.concat(bonusConsents));
        this.props.updateConsentsStates(bonusConsents);
    }

    agreeAllCheckboxChange() {
        if(this.isAllSelected()){
            this.uncheckAllConsents();
        } else {
            this.acceptAllConsents();
        }
    }
    getNewGroupedConsents(){
        var consentsListByPriority = [];
        this.props.consentTypesList.forEach(group => {
            consentsListByPriority.push({
                  listNo: group.listNumber,
                  listTitleProp: group.groupTitle,
                  code: group.code,
                  priority: group.priority,
                  preamble: group.preamble,
                  required: !!group.isRequired,
                  expandedChannels: group.expandedChannels,
                  backgroundColor: group.backgroundColor,
                  sections: group.sections,
                  description: group.description,
                  confirmationDescription: group.confirmationDescription,
                  confirmationMessage: group.confirmationMessage,
                  confirmationRequiredConsentCodes: group.confirmationRequiredConsentCodes,
                  consents:  this.props.consentProps.consents.filter(consent => {
                                    let exist=group.consentsCode.find(code => code===consent.consentCode);
                                    return !!exist;
                             })
            });
        });
        return this.addHasAlreadyAgreedProp(consentsListByPriority.sort((a, b) => a.priority-b.priority));

    }
    getGroupedConsents() {
        let groupedConsents = this.getListConditions().map(c => {
            return {
                listNo: c.listNo,
                listTitleProp: c.listTitleProp,
                preamble: c.preamble,
                description: c.description,
                confirmationDescription: c.confirmationDescription,
                confirmationMessage: c.confirmationMessage,
                confirmationRequiredConsentCodes: c.confirmationRequiredConsentCodes,
                sections: c.sections,
                expandedChannels: c.expandedChannels,
                consents: this.props.consentProps.consents.filter(consent => {
                    let matchAnyCondition = false;
                    c.conditions.forEach(cond => {
                        matchAnyCondition = matchAnyCondition || this.matchCondition(consent, cond);
                    });
                    return matchAnyCondition;

                }).map(consent => {

                    //set priority
                    var condition;
                    var priority = 0;
                    c.conditions.forEach(cond => {
                        if (this.matchCondition(consent, cond)) {
                            if (priority < cond.priority) {
                                condition = cond;
                                priority = cond.priority;
                            }
                        }
                    });
                    consent.rulePriority = condition.priority;

                    return {...consent};
                })

            }
        });
        groupedConsents = this.filterConsentsByRulePriority(groupedConsents);
        return this.addHasAlreadyAgreedProp(groupedConsents);
    }

    addHasAlreadyAgreedProp(groupedConsents) {
        return groupedConsents.map(consentGroup =>
            ({...consentGroup,
                hasAlreadyAgreed: !!consentGroup.consents.find(consent => this.consentIsPermanentlyAgreed(consent))
            })
        )
    }

    getVisibleConsents() {
        let groupedConsents = this.props.newConfiguration?this.getNewGroupedConsents():this.getGroupedConsents();
        return groupedConsents.reduce((previousValue, currentValue) => {
            return [...previousValue, ...currentValue.consents];
        },[]);
    }

    consentIsPermanentlyAgreed(consent) {
        return consentIsPermanentlyAgreed(consent, this.props.consentProps.consentStates);
    }

    channelIsWWW() {
        return this.props.channels.sales === 'WWW';
    }

    getHeadPreamble() {
        if (!this.props.isNet) {
            return this.props.headPreamble
        } else {
            return this.props.headPreamble.filter(hP => !!hP.legalFormList && hP.legalFormList.includes(this.props.legalForm))
        }
    }

    getFilterConfig(){
        if (!this.props.isNet) {
            return false;
        } else if (LegalForm.isSmallBusiness(this.props.legalForm)) {
            return "1114";
        } else {
            return "1014";
        }
    }

    handleRegulationsDownloadClick(event) {
        event.preventDefault();
        if (event.target && event.target.dataset && event.target.id === "regulationsDownloadLink") {
            let document = this.getDocumentItemByDocumentCode(event.target.dataset.documentCode);
            if (document && document.bundleNo) {
                this.props.getOrCreateDocument(document.documentCode, document.bundleNo, document.documentType);
            }
        }
    }

    getDocumentItemByDocumentCode(documentCode) {
        return this.props.documentItems && this.props.documentItems.length
            && this.props.documentItems.find(document => documentCode === document.documentCode)
    };

    render() {
        this.preambleStatuses = [{isGroupRequired: true, shown: false}, {isGroupRequired: false, shown: false}];
        let consentsList = this.props.newConfiguration?this.getNewGroupedConsents():this.getGroupedConsents();
        const isConsentErrorMessage = !!this.props.errorList && !!this.props.errorList.find(e => 'consents' === e.type);
        const showMainError = isConsentErrorMessage && consentsList.find(cl => cl.listNo === 0) && consentsList.find(cl => cl.listNo === 0).consents.find(consent => !validateConsent(consent, this.props.consentProps.consentStates));

        let isDeliveryStep=(this.props.currentStepId==='delivery-payment');
        const componentVisible = this.getMatchingConsents().length > 0 && (!isDeliveryStep || this.props.agreementType);
                console.log("showMainError",showMainError);

                console.log(this.props);
                console.log(isConsentErrorMessage);
        let headPreamble = this.getHeadPreamble();
        let showSogEshopRegulationInfo = this.props.isOnlySogProcess;
        let regulationInfoMessage = this.props.descriptions && this.props.descriptions.regulationInfoMessage;
        let divClassName=(componentVisible ? (isDeliveryStep?"g-gray2-bg":"l-full-row u-padding-l-xl") : "");
        return (

            <div className={divClassName} id={this.elementId} key={this.elementId}>

                <div className={"l-page-row "+(isDeliveryStep?"":"u-padding-top-l-xl")}>

                    {showSogEshopRegulationInfo && regulationInfoMessage && (
                        <div className="l-row u-padding-l-xl">
                            <div className="l-col l-col-small-12 l-col-medium-12 l-col-12">
                                <div className="l-col-9 l-col-medium-12 l-col-small-12">
                                    <p className="u-margin-m u-margin-top-l">
                                    <span
                                        onClick={this.handleRegulationsDownloadClick.bind(this)}
                                        dangerouslySetInnerHTML={{__html: regulationInfoMessage}}>
                                    </span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    )}
                {componentVisible && (
                    <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-12">
                        {!isDeliveryStep && <div>
                            <div className="u-position-relative">
                                <h1 className="h1 u-margin-l">{this.props.descriptions.title}</h1>
                            </div>
                            <PreambleWithExpander value={headPreamble[0]}/>
                        </div>}
                        {this.renderConfirmationModal()}

                        <Expander  className={"opl-agreements "+(isDeliveryStep?"g-gray2-bg u-border-bottom u-padding-l":"")} scrollToSelected={false} variant="" id="consents-component-expander">

                                {!isDeliveryStep && consentsList.map((list, i) => (list.listNo === 0  || (list.priority===10 && this.props.newConfiguration))? (
                                    <div className="opl-agreements__all opl-agreements__all-no-arrow u-padding-all u-large-padding-left-l u-large-margin-minus-left-l u-large-margin-minus-right-l g-yellow2-bg">
                                      <OraSimpleCheckbox {...this.getPropsForCheckbox()} />
                                    </div>):''
                                 )}
                                {consentsList.filter(c => c.code != this.getFilterConfig()).map((list, i) => list.listNo === 0 ? (
                                        this.renderSectionWithSelectAllCheckBox(list)
                                    )
                                    :
                                    <div key={"consentsGroupSection" + i}>
                                        {this.renderPreambleForGroup(list)}
                                        <MulticartConsentsGroupSection key={"consentsGroup" + i}
                                            sectionKey={"consentsGroup" + i}
                                            isDeliveryStep={isDeliveryStep}
                                            consentsErrorMsg={isConsentErrorMessage}
                                            {...this.getPropsForConsentGroup(list.consents)}
                                            title={this.props.newConfiguration?list.listTitleProp:this.props.descriptions[list.listTitleProp]}
                                            id={list.listTitleProp}
                                            {...this.props}
                                            sequence={i}
                                            listNo={list.listNo}
                                            hasAlreadyAgreed={list.hasAlreadyAgreed}
                                            showPrintDocumentsButton={this.printDocumentButtonIsVisible.apply(this)}
                                            initiallyOpen={list.expandedChannels.includes(this.props.channels.sales)}
                                            backgroundColor={list.backgroundColor}
                                            sections={list.sections}
                                            description={list.description}
                                            confirmationDescription={list.confirmationDescription}
                                            confirmationMessage={list.confirmationMessage}
                                            confirmationRequiredConsentCodes={list.confirmationRequiredConsentCodes}
                                            />
                                    </div>
                                )}
                                {isDeliveryStep && consentsList.map((list, i) => list.listNo === 0 ? (
                                    <div className="u-margin-top">
                                        {this.renderSelectAllCheckbox(list)}
                                    </div>
                                ) : ''
                                )}
                        </Expander>
                     </div>
                     </div>
                )}
                {!(this.props.currentStepId==='delivery-payment') && <MulticartValidationComponent messageType="consents" show={!!showMainError} className="u-padding-l"/>}
                </div>
             </div>
        );
    }

    isPreambleShownForGroupRequirity(isGroupRequired) {
        return this.preambleStatuses.find(status => status.isGroupRequired === isGroupRequired).shown;
    }

    setPreambleShownForGroupRequirity(isGroupRequired) {
        this.preambleStatuses = this.preambleStatuses.map(status => {
            if(status.isGroupRequired !== isGroupRequired){
                return status;
            }
            return {...status, shown: true};
        });
    }

    renderPreambleForGroup(group) {
        let allConsentCodes = this.props.consentProps.consents ? this.props.consentProps.consents.map(consent => consent.consentCode) : [];
        let consentCodesInGroup = this.getConsentCodesForGroup(group);
        if (group.preamble && _.intersection(allConsentCodes, consentCodesInGroup).length > 0 && !this.isPreambleShownForGroupRequirity(group.required)) {
            this.setPreambleShownForGroupRequirity(group.required);
            return <div dangerouslySetInnerHTML={{__html: group.preamble}}></div>;
        }
        return null;
    }

    getConsentCodesForGroup(group) {
        if (this.channelIsWWW()) {
            return this.getNotPermanentlyAgreedConsentCodesForGroup(group);
        }
        return group.consents ? group.consents.map(consent => consent.consentCode) : [];
    }

    getNotPermanentlyAgreedConsentCodesForGroup(group) {
        let permanentlyAgreedConsentCodes = this.props.consentProps.consents ? this.props.consentProps.consents
            .filter(consent => this.consentIsPermanentlyAgreed(consent))
            .map(consent => consent.consentCode) : [];
        return group.consents ? group.consents
            .map(consent => consent.consentCode)
            .filter(consentCode => !permanentlyAgreedConsentCodes.includes(consentCode)) : [];
    }

    renderSectionWithSelectAllCheckBox(list){
        return (
            <div className={(this.props.currentStepId==='delivery-payment'?"g-white1-bg u-padding-all-l u-padding-top-l":"u-margin-l")}>
                <ul className="opl-agreements-list u-no-margin">
                    {list.consents.map((consent, index) => <MulticartSingleConsentRow {...this.getPropsForConsent(consent)} descriptions={this.props.descriptions} isDeliveryStep={ this.props.currentStepId==='delivery-payment'} consentsErrorList={this.props.errorList && this.props.errorList.find(e => "consents" === e.type)}/>)}
                </ul>
                {this.props.currentStepId!=='delivery-payment' && this.renderSelectAllCheckbox(list)}
            </div>
        )
    }

    renderSelectAllCheckbox(list){
        return (list.consents && (list.consents.length > 1 || (list.consents.length == 1 && list.consents[0].bundleAssignments && list.consents[0].bundleAssignments.length > 1))) ?
                               (
                                 <div className="u-spacing-l u-padding-all g-yellow2-bg">
                                    <OraSimpleCheckbox {...this.getPropsForCheckbox()} />
                                </div>
                             ) :  ""

    }

    filterConsentsByRulePriority(consentsList) {
        var consentsListByPriority = [];

        consentsList.forEach(listA => {
            var consents = [];
            listA.consents.forEach(consentA => {
                var isUnique = true;

                consentsList.forEach(listB => {
                    listB.consents.forEach(consentB => {
                        if (consentA.consentCode == consentB.consentCode && listA.listNo != listB.listNo) {
                            isUnique = false;
                            if (consentA.rulePriority >= consentB.rulePriority) {
                                consents.push(consentA);
                            }
                        }
                    });
                });

                //if not found
                if (isUnique == true) {
                    consents.push(consentA);
                }

            });

            consentsListByPriority.push({
                listNo: listA.listNo,
                listTitleProp: listA.listTitleProp,
                preamble: listA.preamble,
                expandedChannels: listA.expandedChannels,
                consents: consents
            });
        });
        return consentsListByPriority;
    }

    matchCondition(consent, condition) {

        if ((condition.withBonus === null ? true : condition.withBonus === consent.isRelatedWithBonus) &&
            (condition.isRequired === null ? true : condition.isRequired === consent.required)) {

            if (condition.consents.length === 0) {
                //default
                return true;
            } else {
                for (var i = 0; i < condition.consents.length; i++) {
                    if (consent.type === condition.consents[i]) {
                        return true;
                    }
                }
            }
        }

        return false;
    }

    getListConditions() {
        let uniqueSet = new Set();
        this.props.consentTypesList.map(ctl => ctl.listNumber).forEach(el => uniqueSet.add(el));
        let uniqueListNo = [];
        uniqueSet.forEach(use => uniqueListNo.push(use));
        uniqueListNo = uniqueListNo.sort();

        return uniqueListNo.map(listNo => {
            return {
                listNo: listNo,
                listTitleProp: "listTitle" + listNo,
                preamble: this.props.consentTypesList.find(group => group.listNumber === listNo).preamble,
                description: this.props.consentTypesList.find(group => group.listNumber === listNo).description,
                expandedChannels: this.props.consentTypesList.find(group => group.listNumber === listNo).expandedChannels,
                sections: this.props.consentTypesList.find(group => group.listNumber === listNo).sections,
                conditions: this.props.consentTypesList.filter(ctl => ctl.listNumber === listNo)
            }
        });
    }
}

const mapStateToProps = state => ({
    registeredCheckoutConditions: getRegisteredCheckoutConditions(state),
    consentProps: getConsentsProps(state),
    selectedMethod: getSelectedDeliveryMethod(state),
    isPrintConsentDocumentsButtonEnabled:isPrintConsentDocumentsButtonEnabled(state),
    customerNoEmail: getCustomerNoEmail(state),
    clearingModifyConsentsInCartQueueInProgress: getModifyConsentsInCartQueue(state) > 0,
    errorList: getFrontValidationMsg(state),
    isNet : getCartIsNet(state),
    legalForm : getLegalForm(state),
    currentStepId: getCurrentStepId(state),
    consentsDataInRequest: consentsDataInRequest(state),
    info: "Wydrukuj zgody na weryfikacj w systemach zewntrznych (BIG, ZONK)",
    buttonText: "Drukuj",
    agreementType: getAgreementType(state),
    isOnlySogProcess: getSalesOfGoodsProcess(state),
    documentItems: getDocumentItems(state),
    isLogged: isLogged(state),
});

const MulticartConsentsComponent = connect(
        mapStateToProps,
    {
        changeConsentState,
        updateConsentsStates,
        requestCartConsentsData,
        registerCmsConsentConfig,
        registerNextStepCondition,
        registerVerificationConsentType,
        unregisterNextStepCondition,
        printConsents,
        printMobileConsents,
        registerPrintConsentValidator,
        pushToModifyConsentsInCartQueue,
        clearModifyConsentsInCartQueue,
        setPermanentlyAgreedConsentsVisibleForGroup,
        updateConsentGroupExpanded,
        updateValueConsentGroupExpanded,
        getOrCreateDocument

    }
    )(MulticartConsentsView);
export default MulticartConsentsComponent;



var titleWithRequiredLabelIdIndex = 2;
class TitleWithRequiredLabel extends Component {

    constructor(props) {
        super(props);
        this.index = titleWithRequiredLabelIdIndex++;
        this.id = "titleWithRequiredLabel-" + this.index;
        this.fixerGroup = "js-layout-fixer-group-titleWithRequiredLabel-" + this.index
    }

    layoutFixerModule(){
        return {
            path: "core/modules/layout-fixer",
            conditions: "element:{was seen}",
            options: {
                selectors: ["."+this.fixerGroup]
            }
        }
    }

    componentDidMount() {
        OPL.UI.loadModules(document.getElementById(this.id), [this.layoutFixerModule()]);
    }

    componentWillUnmount() {
        OPL.UI.stopModules(document.getElementById(this.id));
    }

    render(){
        return (
            <div className="l-row u-spacing-l">
                <div id={this.id} >
                    <div className="l-col l-col-10 l-col-small-9">
                        <div className={"u-animate-height " + this.fixerGroup}>
                            <div className="js-height-sensitive-element">
                                <p className="h2">{this.props.title}</p>
                            </div>
                        </div>
                    </div>
                    <div className="l-col l-col-2 l-col-small-3">
                        <div className={"u-animate-height " + this.fixerGroup}>
                            <div className="js-height-sensitive-element">
                                <span className="u-position-bottom u-position-absolute u-position-right u-margin-right">{"* " + this.props.requiredLabel}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

class PreambleWithExpander  extends Component {

    getHtmlForExpander(){
        if(this.props.value && this.props.value.header){
            return '<div>' +
                this.props.value.header +
                (this.props.value.more ? '<div id="EXPANDER-ID-PLACEHOLDER">'+this.props.value.more + '</div>' : '') +
            '</div>'
        } else {
            return null
        }
    }

    render(){
        let html = this.getHtmlForExpander();
        return html ? <p className="u-padding-l"><ExpanderFromHtml html={html}/></p> : null
    }
}
